import css from './Header.module.css'
import {NavLink} from "react-router-dom";
const Header = (props) =>{
    return(
        <header className={css.header}>
            <img className={css.header__logo} alt={'logo'}
                 src='https://png.pngtree.com/element_our/png/20180912/coffee-time-png_91570.jpg'/>

            <div className={css.login__block}>
                { props.isAuth
                    ?<div> {props.login} <div><button onClick={props.logout}>Logout</button></div></div>
                    :<NavLink to={'/login'}>Login</NavLink>
                }
            </div>
        </header>
    )
}
export default Header;