import classes from './Navbar.module.css'
import {NavLink} from "react-router-dom";
import Friends from "./FriendsList/Friends";


const Nav = (props) => {
    let friendsList = props.friends.map(friend => <Friends name={friend.name} key={friend.id} avatar={friend.avatar}/>)
    return (
        <nav className={classes.nav}>
            <div className={classes.item}><NavLink to='/profile'
                                                   className={navData => navData.isActive ? classes.active : classes.item}>Profile</NavLink>
            </div>
            <div className={classes.item}><NavLink to='/messages'
                                                   className={navData => navData.isActive ? classes.active : classes.item}>Messages</NavLink>
            </div>
            <div className={classes.item}><NavLink to='/news'
                                                   className={navData => navData.isActive ? classes.active : classes.item}>News</NavLink>
            </div>
            <div className={classes.item}><NavLink to='/music'
                                                   className={navData => navData.isActive ? classes.active : classes.item}>Music</NavLink>
            </div>
            <div className={classes.item}><NavLink to='/settings'
                                                   className={navData => navData.isActive ? classes.active : classes.item}>Settings</NavLink>
            </div>
            <div className={classes.item}><NavLink to='/users'
                                                   className={navData => navData.isActive ? classes.active : classes.item}>Users</NavLink>
            </div>
            <div className={classes.friendsTitle}>Friends</div>
            <div className={classes.friends}>{friendsList}</div>
        </nav>)
}

export default Nav