const SEND_MESSAGE = 'SEND-MESSAGE';

let initialState = {
    dialogs: [
        {id: 1, name: "Oleg", avatar: "https://i.ytimg.com/vi/V78xVbsVr-M/hqdefault.jpg"},
        {
            id: 2,
            name: "Andrey",
            avatar: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSI-_kgOmA9pVU8v0-y9RPdW7v4EHj-FoAkwA&usqp=CAU"
        },
        {
            id: 3,
            name: "Borya",
            avatar: "data:image/jpeg;base64,"
        },
        {
            id: 4,
            name: "Max",
            avatar: "https://mdbcdn.b-cdn.net/img/new/avatars/2.webp"
        },
        {id: 5, name: "Boris Andreevich", avatar: "https://i.ytimg.com/vi/yuE6-wrQ5X4/maxresdefault.jpg"}],
    message: [
        {id: 1, message: "Hello dude! How a u?"},
        {id: 2, message: "Nah, don't feel like it..."},
        {id: 3, message: "Ikr?"}],
}


const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case SEND_MESSAGE:
            let text = action.messageArea;
            return {
                ...state,
                newMessageText: '',
                message: [...state.message, {id: 4, message: text}]
            }


        default:
            return state;
    }
}

export const sendMessageActionCreator = (messageArea) => ({type: SEND_MESSAGE, messageArea})




export default dialogsReducer;