import {authAPI} from "../api/api";

const SET_USER_DATA = 'auth/SET_USER_DATA';


let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    isFetching: false
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:

            return {
                ...state,
                ...action.data,
            }


        default:
            return state;
    }
}

export const setAuthUserData = (userId, email, login, isAuth) => ({
    type: SET_USER_DATA,
    data: {userId, email, login, isAuth}
})


export const getAuthUserData = () => async (dispatch) => {
    let data = await authAPI.myProfile()
    if (data.resultCode === 0) {
        let {id, email, login} = data.data;
        dispatch(setAuthUserData(id, email, login, true));
    }

}

export const login = (email, password, rememberMe, setStatus) => async (dispatch) => {
    let data = await authAPI.login(email, password, rememberMe, setStatus)
    if (data.resultCode === 0) {
        dispatch(getAuthUserData())
    } else {
        setStatus({message: data.messages})
        console.log(setStatus.message)

    }

}

export const logout = () => async (dispatch) => {
    let data = await authAPI.logout()
    if (data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false));

    }

}


export default authReducer;
